body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: white no-repeat center center fixed;
  background-size: cover;
  overflow: auto;
}
     
.container {
  text-align: center;
  margin-left: 23%;
  margin-right: 23%;
  font-family: "Roboto", sans-serif;
  /* background-color: black; */
}

.druggler {
  font-family: "Ubuntu", sans-serif;
}

.container2 {
  text-align: center;
  /* margin-left: 23%;
    margin-right: 23%; */
  background-color: seagreen;
  width: 100%;
  height: 90.2%;
  font-family: "Roboto", sans-serif;
}

.card-heading {
  background-color: seagreen;
  padding-Top: 5%;
  padding-Bottom: 5%;
  padding-Left: 2%;
  margin-Right: 1%;
  margin-top: 0;
  margin-bottom: 0;
  color: white;
  display: inline-block;
  font-size: 50px;
  font-family: "Ubuntu", sans-serif;
}

.button-line {
  color: white;
}

.raised-button {
  box-shadow: 3px 30px 5px 2px rgba(255, 105, 135, 0.8);
  background-color: lime;
}

p {
  color: white;
  font-family: "Roboto", sans-serif;
}

.navTitle {
  display: inline-block;
}

p {
    font-size: 20px;
    font-weight: 400;
    font-family: 'Darker Grotesque', sans-serif;
}

/* .appBar {
    height: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
} */

.appBar2 {
    height: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: seagreen;
}

.navTitle {
    font-family: "Constantia";
    font-weight: 800;
    font-size: 30pt;
    float: left;
    margin-left: 5%;
    color: white;
}

.navButton {
    font-family: 'Darker grotesque', sans-serif;
    font-weight: 400;
    font-size: 16pt;
    float: right;
    margin-right: 5%;
    margin-top: 15px;
    bottom: 0px;
    flex-wrap: wrap;
    color:  white;
    text-decoration: none;
}

.spacer {
    height: 20px;
    width: 100%;
    background-color: blue;
}
.navButton:hover{
    cursor: pointer;
    color: black;
}
.link:hover{
    cursor: pointer;
    color: rgb(102, 3, 3);
}

@media only screen and (max-width: 700px) {
    .navTitle {
        font-size: 22pt;
    }
}

@media only screen and (max-width: 430px) {
    .navTitle {
        font-size: 12pt;
    }
}

@media only screen and (max-width: 700px) {
    .navButton {
        font-size: 12pt;
        margin-top: 8px;
    }
}

@media only screen and (max-width: 488px) {
    .navButton {
        font-size: 8pt;
    }
}

@media only screen and (max-width: 700px) {
    .appBar {
        height: 40px;
    }
}

@media only screen and (max-width: 700px) {
    .appBar2 {
        height: 40px;
    }
}
